import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// import Home from './components/Hello'
// import Head from './components/Head'

// import Detail from './components/pages/Detail'
// import Act from './components/pages/Act'
// import Me from './components/pages/Me'
// import News from './components/pages/News'

const Home = resolve => require(['./components/Hello'], resolve)
const Detail = resolve => require(['./components/pages/Detail'], resolve)
const Act = resolve => require(['./components/pages/Act'], resolve)
const Me = resolve => require(['./components/pages/Me'], resolve)
const News = resolve => require(['./components/pages/News'], resolve)

// const newsList = resolve => require(['./components/models/newsList'], resolve)
import newsList from './components/models/newsList'

const router = new VueRouter({
  // mode: 'history',
  routes: [{
    // 首页路由配置
    path: '',
    redirect: '/home'
  }, {
    path: '/home',
    component: Home
  }, {
    path: '/news',
    component: News,
    children: [
      {
        path: '',
        redirect: 'all'
        // components: {
        //   'news-list': newsList
        // }
      },
      {
        path: ':keywords',
        components: {
          'news-list': newsList
        }
      }
    ]
  }, {
    path: '/detail/:picId',
    name: 'detail',
    components: {
      outter: Detail
    }
  }, {
    path: '/active',
    component: Act
  }, {
    path: '/me',
    component: Me
  }, {
    path: '/home',
    redirect: '/home'
  }]
})

export default router



