<template>
  <div class="news-view">
    <!-- <h3>qqqq</h3>
    <p>{{ keywords }}</p>
    <p>{{ cnode_topic }}</p> -->
    <p>{{cnode_topic}}</p>
    <ul>
      <li v-for="item in lists" class="news-view-list cfx">
        <a class="news-view-list-left fl">
          <div class="author-avatar">
            <img :src="item.author.avatar_url" alt="">
          </div>
        </a>
        <a class="news-view-list-right fl"  @click="showDetail(item)" >
          <div class="cfx">
            <div class="nvlr-left fl" :class="item.top ? 'tab-top' : item.good ? 'tab-good' : ('tab-' + item.tab)">
                {{ item.top ? '置顶' : item.good ? '精华' : item.tab === 'share' ? '分享' : item.tab === 'ask' ? '问答' : '' }}
            </div>
            <div class="nvlr-middle fl">
              {{ item.title }}
            </div>
            <div class="nvlr-right fr">
              <!-- create_at 发布时间 -->
              <!-- {{ processDate(item.create_at) }} -->
              <!-- last_replay_at 最后回复时间 -->
              {{ processDate(item.last_reply_at) }}
            </div>
          </div>
        </a>
      </li>
    </ul>

    <div class="news-view-detail" v-show="isShow" id="news-view-detail">
      {{  }}
    </div>
    <div class="news-view-back"  v-show="isShow" @click="isShow=!isShow">返回</div>
  </div>
</template>
<script>
  import axios from 'axios'

  export default{
    data () {
      return {
        // keywords: 'ddd',
        newsLists: '',
        lists: '',
        news: {
          'content': '暂无内容',
          'title': '暂无标题'
        },
        isShow: false
      }
    },
    mounted () {
      // this.getTopic2()
    },
    computed: {
      keywords () {
        return this.$route.path
      },
      cnode_topic () {
        this.lists = this.getTopic2()
        // return this.getTopic2()
      }
    },
    methods: {
      getTopic () {
        let _this = this
        let type = this.$route.params.keywords
        type = type === 'all' ? '' : type
        let url = 'https://cnodejs.org/api/v1/topics'
        console.log(type)
        // let data = {
        //   page: 10,
        //   tab: type,
        //   limit: 30,
        //   mdrender: true
        // }
        // let xhr = new XMLHttpRequest() || new ActiveXObject('Microsoft.XMLHTTP')
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = function () {
          if (xhr.readyState === 4) {
            if (xhr.status === 200) {
              console.log('succ')
              let res = JSON.parse(xhr.responseText)
              console.log(res)
              _this.lists = res.data
              return res.data
            }
          }
        }
        xhr.open('get', url, true)
        xhr.send(null)
      },
      getTopic2 () {
        let url = 'https://cnodejs.org/api/v1/topics'
        let type = this.$route.params.keywords
        console.log(type)
        let _this = this
        type = type === 'all' ? '' : type
        axios.get(url, {params: {
          tab: type
        }}).then(function (res) {
          _this.lists = res.data.data
          return res.data.data
        }).catch(function (error) {
          console.log(error)
        })
      },
      showDetail (item) {
        document.getElementById('news-view-detail').innerHTML = item.content
        this.isShow = !this.isShow
        // this.newsContent = item.content
        // alert(JSON.stringify(item.content))
      },
      processDate (timestamp) {
        let diff = +new Date() - (+new Date(timestamp))

        let year = 1000 * 3600 * 24 * 365
        let month = 1000 * 3600 * 24 * 30
        let day = 1000 * 3600 * 24
        let hour = 1000 * 3600
        let minute = 1000 * 60

        let res = '侏罗纪'

        if (diff > year) {
          res = Math.floor(diff / year) + '年前'
        } else if (diff > month) {
          res = Math.floor(diff / month) + '个月前'
        } else if (diff > day) {
          res = Math.floor(diff / day) + '天前'
        } else if (diff > hour) {
          res = Math.floor(diff / hour) + '小时前'
        } else if (diff > minute) {
          res = Math.floor(diff / minute) + '分钟前'
        } else if (diff < minute) {
          res = Math.floor(diff / minute) + '秒前'
        }
        return res
      }
    }
  }
</script>
<style>
  .news-view{
    position: relative;
    margin: 48px 0;
    text-align: left;
  }
  .news-view-list{
    margin: 10px 10px;
    padding: 15px 0;
    box-shadow: 0px 2px 1px #eee;
    background-color: #fbfbfb;
    font-size: 12px;
    border-radius: 5px;
  }
  /*内容*/
  .news-view-detail .markdown-text{
    padding-bottom: 20px;
  }
  .news-view-detail{
    position: fixed;
    left: 0;
    top: 40px;
    bottom: 40px;
    background-color: #fff;
    width: 92%;
    height: 90%;
    overflow-y: auto;
    padding: 0 4%;
    padding-bottom: 0px;
    word-wrap:break-word;
    word-break:normal;
    font-size: 14px;
  }
  @media screen and (min-width: 900px){
    .news-view-detail{
      max-width: 800px;
      left: 50%;
      margin-left: -400px;
      padding: 0;
    }
  }
  .news-view-detail img{
    width: 98%;
  }
  .news-view-detail table{
    max-width: 98%;
    zoom: 0.8;
    padding: 0;
    border-collapse: collapse;
    border-spacing: 2px;
    border: 1px solid #999;
    cellpadding: 10;
    cellspacing: 2px;
  }
  .news-view-detail h1{
    font-size: 22px;
    padding: 10px 0;
  }
  .news-view-detail h2{
    font-size: 20px;
    padding: 10px 0;
  }
  .news-view-detail h3{
    font-size: 18px;
    padding: 10px 0;
  }
  .news-view-detail p{
    text-indent: 20px;
    padding: 3px 0;
  }
  .news-view-detail a{
    color: #42b983;
  }
  .news-view-detail ul, .news-view-detail ol{
    list-style: disc;
    padding: 20px 0;
    padding-left: 20px;
  }
  .news-view-detail ul li, .news-view-detail ol li{
    padding: 5px 0;
    font-size: 12px;
    color: #666;
  }
  .news-view-detail pre{
    display: block;
    overflow: auto;
    background-color: #eee;
    word-wrap:break-word;
  }
  .news-view-detail code{
    display: block;
    overflow: auto;
    word-wrap:break-word;
  }
  .news-view-detail pre, .news-view-detail code{
    padding: 10px;
    font-family: 'Microsoft Yahei';
  }
  .news-view-back{
    position: fixed;
    top: 8px;
    right: 4%;
    padding: 3px 5px;
    z-index: 2;
    font-size: 12px;
    color: #fff;
    border: 0px solid #bbb;
    border-radius: 5px;
    background-color: #42b983;
  }
  @media screen and (min-width: 900px){
    .news-view-back{
      right: 50%;
      margin-right: -380px;
    }
  }
  /*内容*/
  .news-view-list-left{
    width: 10%;
    text-align: center;
  }
  .news-view-list-right{
    margin-top: 8px;
    width: 90%;
  }
  .author-avatar img{
    max-width: 30px;
    height: auto;
    border-radius: 3px;
  }
  .nvlr-left{
    width: 8%;
    margin-right: 2%;
  }
  .nvlr-middle{
    width: 70%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #222;
  }
  .nvlr-right{
    width: 15%;
    text-align: right;
    color: #999;
    margin-right: 5px;
  }
  .tab-top, .tab-good, .tab-share, .tab-ask{
    color: #fff;
    border-radius: 3px;
    text-align: center;
  }
  .tab-top{
    background-color: #f00;
  }
  .tab-good{
    background-color: #FF7F24;
  }
  .tab-share{
    background-color: #42b983;
  }
  .tab-ask{
    background-color: #999;
  }
</style>



