<template>
  <div class="foot" v-show="lists.length">
    <ul class="nav">
      <!-- <li><router-link to="/home">首页</router-link></li>
      <li><router-link to="/news">新闻</router-link></li>
      <li><router-link to="/active">活动</router-link></li>
      <li><router-link to="/me">我</router-link></li> -->
      <li v-for="list in lists">
        <router-link :to="list.path">
          <div class="nav-list"  @click="changeActive(list)">
            <img :src="list.isCur ? list.icon.active : list.icon.default" alt="">
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        lists: [
          {
            'path': '/home',
            'title': '首页',
            'isCur': true,
            'icon': {
              'default': 'data:image/png;base64,',
              'active': 'data:image/png;base64,'
            }
          },
          {
            'path': '/news',
            'title': '新闻',
            'isCur': false,
            'icon': {
              'default': 'data:image/png;base64,',
              'active': 'data:image/png;base64,'
            }
          },
          {
            'path': '/active',
            'title': '活动',
            'isCur': false,
            'icon': {
              'default': 'data:image/png;base64,',
              'active': 'data:image/png;base64,'
            }
          },
          {
            'path': '/me',
            'title': '我',
            'isCur': false,
            'icon': {
              'default': 'data:image/png;base64,',
              'active': 'data:image/png;base64,'
            }
          },
          {
            'path': '/news',
            'title': '发现',
            'isCur': false,
            'icon': {
              'default': 'data:image/png;base64,',
              'active': 'data:image/png;base64,'
            }
          }
        ]
      }
    },
    methods: {
      changeActive (a) {
        for (var i = 0, len = this.lists.length; i < len; i++) {
          this.lists[i].isCur = false
        }
        a.isCur = !a.isCur
      }
    }
  }
</script>
<style>
  .foot{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    background-color: #fff;
    color: #fff;
    z-index: 99;
    box-shadow: 0px -1px 1px #eee;
  }
  @media screen and (min-width: 900px){
    .foot{
      max-width: 800px;
      left: 50%;
      margin-left: -400px;
    }
  }
  .nav{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav li{
    flex: 1;
    height: 100%;
    flex-direction: column;
    vertical-align: middle;
  }
  .nav li a{
    text-decoration: none;
    display: block;
    height: 40px;
    line-height: 40px;
  }
  .nav-list{text-align: center;}
  .nav-list img{width: 26px;margin-top: 6px;}
</style>



